.keypad {
  box-sizing: border-box;
  width: 50%;
  height: 100%;
  padding: 40px;
  display: grid;
  grid-template-columns: repeat(3, 25%);
  grid-template-rows: repeat(3, 25%);
  grid-gap: 12.5%;
  background-color: #f2f2f2;
}
.keypad button {
  background-color: #fff;
  box-shadow: 0 0 0 5px rgba(0, 0, 0, 0.5019607843);
  font-size: 1.1em;
}
.keypad button:hover {
  transform: scale(1.1);
  box-shadow: none;
}
.keypad button:active {
  transform: scale(0.9);
  box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.5019607843);
}


